$primary-color: #407c6c;

@import 'animate.css';
@import '~loaders.css/src/animations/cube-transition.scss';

@font-face {
  font-family: 'Aviano-Sans-Light';
  src: url('./assets/fonts/Aviano-Sans-Light.ttf') format('truetype');
}

@font-face {
  font-family: 'YesevaOne-Regular';
  src: url('./assets/fonts/YesevaOne-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Helvetica-Regular';
  src: url('./assets/fonts/Helvetica.ttf') format('truetype');
}

input, 
textarea, h1, h2, h3, a {
  font-family: 'YesevaOne-Regular';
}

p{
  
  font-family: 'Helvetica-Regular';
}
// Scrollbar
::-webkit-scrollbar{

  width: 0px;
  height: 90%;
}
::-webkit-scrollbar-track{
  border-radius: 5px;
  box-shadow: inset 0 0 10px rgba(0,0,0,0.25);
}
::-webkit-scrollbar-thumb{
  border-radius: 5px;
  background: #069370;
}


// Boutton
.button-container /* only for grid section*/ {
  height: 150px;
  background:  linear-gradient(0deg, rgba(6,147,112,1) 0%, rgba(255,255,255,0.35898109243697474) 100%);
  display: flex;
  justify-content: center;
  align-items: center;
}
.button {
  width: 150px;
  height: 50px;
  background: #fff;
  margin: 10px auto;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 5px 5px 5px rgba(0,0,0,0.2);
  backdrop-filter: blur(5px);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s;
  cursor: pointer;

  a{
    text-decoration: none;
    color: black;
    font-family: 'YesevaOne-Regular';
    font-size: 1em;
    font-weight: 600;
  }

  &:hover{
    background: #CEE0C7;
    box-shadow: inset 0 0 15px 3px rgba(0,0,0,0.2);;
    box-shadow: 5px 5px 5px rgba(0,0,0,0.2);
  }
  // &:hover a{
  //   color: #fff;
  // }
}

// Loader
.loader-active {
  display: block;
  position: fixed;
  left: 0;
  right: 0;
  margin: auto;
  top: 0;
  bottom: 0;
  width: 50px;
  height: 50px;
  animation: fadeOut 1s 1s;
  animation-fill-mode: forwards;
}

// Loading
.first-loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  pointer-events: none;
  margin: 0 auto;
  opacity: 1;
  transition: 1s ease;
  display: flex;
  justify-content: center;
  background: #fff;
  flex-direction: column;
  align-items: center;
  animation: fadeOut 1s 6s;
  animation-fill-mode: forwards;
  

  &.inactive{
    display: none;
    opacity: 0;
  }


    video{
      width: 100%;
      max-width: 800px;
      padding: 20px;
      object-fit: cover;
    }
  

  .button.first{
    pointer-events: all;
    opacity: 0;
    animation: backInUp 1s 4.5s;
    animation-fill-mode: forwards;
  }
} 


.main {
  display: none;
}
