footer{
    bottom: 0;
    width: 100%;
    color: #fff; 
    display: grid;
    place-items: center;
    padding: 0.5em; 
    font-size: 1.2rem;

    img{
        width: 100px;
    }
    .text {
        position: absolute;
        bottom: 0;
        right: 0;
        padding:5px;
        text-align: right;

        p{
            font-family: 'Nunito', sans-serif;
            color: rgb(173, 173, 173);
            font-size: 0.85rem;
            
            a {
                text-decoration: none;
                color: rgb(189, 189, 189);
            }
        }
    }
}
@media screen and (max-width:872px){
    footer{
        padding: 1%;
    }

}

@media screen and (max-width:872px){
    footer{
        padding: 0.5%;
    }

}