/* In my case, I use these CSS styles in my App.css file*/
#slider-entretient {
  width: 100%;
  max-width: 1300px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  
  .swiper {
    position: absolute;
    width: 100%;
    height: 100%;
    .swiper-wrapper{
      height: 100%;
      .swiper-slide {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        
        img {
          display: block;
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }

    .swiper-pagination{

      .swiper-pagination-bullet-active{
          background: rgb(214, 211, 175);
          opacity: 1;
      }
    }
  }
  
}



