
.content-container {
    width: 100%;
    max-width: 1300px;
    position: relative;
    top: -50px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 10px;

    .content {
        position: relative;
        width: 100%;
        max-height: 60vh;
        margin: 10px 0;
    }
    
    .title { 
        grid-area: title; 
        text-align: center;
    
        h1{
            color: black;
            font-family: 'YesevaOne-Regular';
            font-size: 50px;
            font-weight: 300;
            text-shadow: 15px 15px 8px rgba(0,0,0,0.4);
        }
    }
} 

.button.home {
    width: 200px;
    margin-bottom: 70px;
}



@media screen and (max-width:1200px){
    .content-container{
        grid-template-columns: 50% 50%;
        grid-template-rows: 1fr 1fr 1fr 1fr;
    }
}
  
@media screen and (max-width:1100px){
    .content-container {
        width: 100%;
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        flex-direction: column;
        align-items: center;
    }

}

@media screen and (max-width:800px){
    .content-container {
        .content.first{
            margin-top: 15%;
        }
        .content.second{
            margin-top: 32%;
        }
    }
    .button.home {
        margin-top: 160px;
    }
}
@media screen and (max-width:650px){
    .content-container {

        .content.second{
            margin-top: 27%;
        }
    }
    .button.home {
        margin-top: 100px;
    }
}