header{
  z-index: 1000;
  position: absolute;
  width: 100vw;
  margin: 0 auto;
  .header-container{
    width: 95%;
    max-width: 1200px;
    margin: 20px auto;
    padding: 10px;
    background: #fff;
    box-sizing: border-box;
    border-radius: 4px;
    box-shadow: 5px 5px 5px rgba(0,0,0, 0.2);
    opacity: 0;
    animation: backInDown 1s 0.2s;
    animation-fill-mode: forwards;
  
    .logo{
      font-family: 'Yeseva One', cursive;
      color: #000000;
      height: 60px;
      font-size: 36px;
      text-align: center;
      box-sizing: border-box;
      float: left;
  
      img{
        width: 100%;
        height: 100%;
      }
    }
  
    .menu-toggle{
      display: none;
    }
  
    nav{
      float: right;
      
      ul {
        margin: 0;
        padding: 0;
        display: flex;
        gap: 5px;
    
        li {
          list-style: none;
          position: relative;


          &:hover ul{
            display: block;
            left: 0%;
          }
    
          a{
            display: block;
            margin: 10px 0;
            padding: 10px 20px;
            text-decoration: none;
            color: #000000;
            font-size: 17px;
            transition: all 1s ease;
    
            &.active, &:hover {
              background: #CEE0C7;
              box-shadow: inset 0 0 15px 3px rgba(0,0,0,0.2);;
              box-shadow: 5px 5px 5px rgba(0,0,0,0.2);
              border-radius: 5px;
              transition: 0.5s;
            }
          }
        }
      }
    }
  
    .clearfix{
      clear: both;
    }
  }
}



@media screen and (max-width:952px) {
  header {
    .header-container{
      .menu-toggle{
          display: block;
          width: 40px;
          height: 40px;
          padding-top: 4px;
          float: right;
          margin: 10px;
          cursor: pointer;
          text-align: center;
          font-size: 30px;
      }
    
      nav{
          display: none;
          margin-right: 20px;
    
          &.active{
              display: block;
              width: 100%;
              text-align: center;
    
              ul {
                display: block;
                
                li {
                  padding: 5px;
                }
                li a {
                  margin: 0;
                  margin-left: 10%;              
                }
                li:hover ul {
                  position: relative;
                  color: #345e3e;
                }
    
                li ul li {
                  width: 100%;
                }
              }
          }   
    
          ul {
    
            li {
    
              ul {
                margin-left: 10%;
                padding-right: 8%;
                padding-top: 2%;
                padding-bottom: 2%;
              }
    
              &:hover ul {
                display: none;
                left: 0%;
              }
    
              &.active ul{
                display: block;
                left: 0%;
              }
            } 
          }
      }

    }
  }
}