/* In my case, I use these CSS styles in my App.css file*/
#slider-home-first {
  width: 100%;
  max-width: 1300px;
  height: 600px;
  margin: 0 auto;
  position: relative;
  top: -50px;
  right: 0;
  
  .swiper {
    position: absolute;
    width: 100%;
    height: 600px;
    .swiper-wrapper{
      .swiper-slide {
        width: 100%;
        height: 600px;
        
        img {
          display: block;
          width: 100%;
          height: 600px;
          object-fit: cover;
        }
      }
      
      //   .swiper-pagination-bullet{
      //     z-index: 101;
      //     background: #ffffff;
      //     opacity: 1;
      // }
      // .swiper-pagination-bullet-active{
      //     background: rgb(214, 211, 175);
      //     opacity: 1;
      // }
      
    }
  }
  
  .logo {
    z-index: 1;
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    padding-bottom: 50px;
    align-items: center;
    flex-direction: column;
    gap: 3rem;
    background: linear-gradient(to top, #222222 , #22222200 15%);
    
        .overlay{
          z-index: 1;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            transition: 0.5s ease-in-out;
            background: linear-gradient(to top, #22222280 , #22222200 100%);
            opacity: 0;
        }
        img{
            z-index: 2;
            width: 240px;
            height: 240px;
            // display: none;
            opacity: 0;
            animation: backInUp 1s 1.2s;
            animation-fill-mode: forwards;
        }
        p {
            z-index: 2;
            max-width: 80%;
            text-align: center;
            color: rgba(255, 255, 255);
            font-size: 20px;            
            font-weight: 600;
            opacity: 0;
            animation: backInUp 1s 1.2s;
            animation-fill-mode: forwards;
        }
  
    &:hover {
        .overlay{
            z-index: 1;
            opacity: 1;
            transition: 0.5s;
        }
    }
    
  }
}

@media (max-width: 850px) {
  /* In my case, I use these CSS styles in my App.css file*/
   #slider-home-first {

    .logo {

      img{
          z-index: 2;
          width: 200px;
          height: 200px;
      }
      p {
          font-size: 17px;
      }    
    }
  }
}

