.box{
    position: relative;
    top: 0;
    left: 0;
    margin: 0 auto;
    width: 90%;
    max-width: 1300px;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

    .box-content{
        width: 100%;
        height: 70%;
        display: flex;
        flex-direction: column;

        .tab-container{
            width: 100%;
            max-width: 90vw;
            height: auto;
            display: flex;
            flex-wrap: wrap;
            flex-direction: row;
            gap: 1rem;
            margin-bottom: 20px;
            justify-content: center;
            
            .tabs{
                z-index: 999;
                padding: 15px;
                color: #000000;
                transition: 0.2s ease-in-out;
                background: #fff;
                box-sizing: border-box;
                border-radius: 5px;
                box-shadow: 5px 5px 5px rgba(0,0,0, 0.2); 
                cursor: pointer;  
                opacity: 0;
                font-family: 'YesevaOne-Regular';
    
                &.one {
                    animation: fadeInUp 2s 1.3s;
                    animation-fill-mode: forwards;
                }
                &.two {
                    animation: fadeInUp 2s 1.4s;
                    animation-fill-mode: forwards;
                }
                &.three {            
                    animation: fadeInUp 2s 1.5s;
                    animation-fill-mode: forwards;
                }
                &.four {            
                    animation: fadeInUp 2s 1.6s;
                    animation-fill-mode: forwards;
                }
                &:hover,
                &.active-tabs {
                    box-shadow: inset 0 0 15px 3px rgba(0,0,0,0.2);;
                    box-shadow: 5px 5px 5px rgba(0,0,0,0.2);
                    background: #CEE0C7;
                }
            }
        }
    }
    
}
@media (max-width:800px) {
    .box{
        width: 100%;
        .box-content{
            .tab-container{
                gap: 0.5rem;
    
                .tabs{
                    padding: 15px;
                }
            }

        }
    }
}
footer {
    position: absolute;
    ul {
        display: none;
    }
}