// Css Slider 
/* In my case, I use these CSS styles in my App.css file*/
#slider-creation {
  width: 100%;
  max-width: 1300px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  
  .swiper {
    position: absolute;
    width: 100%;
    height: 100%;
    .swiper-wrapper{
      .swiper-slide {
        width: 100%;
        height: 100%;
        
        img {
          display: block;
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }

    .swiper-pagination{

      .swiper-pagination-bullet-active{
          background: rgb(214, 211, 175);
          opacity: 1;
      }
    }
  }
  
}

@media (max-width: 850px) {
  /* In my case, I use these CSS styles in my App.css file*/
  .slider-entretient-gazon {
    height: 300px;
    .swiper {
      height: 300px;
      .swiper-wrapper{
        .swiper-slide {
          height: 300px;
          
          img {    
            height: 300px;
          }
        }
      }
  
    }
  }
}

