.page {
    width: 100%;
    height: 100%;
    position: absolute;
}

.content {
    position: absolute;
    width: 100%;
    height: 100%;
    max-height: 60%;
    opacity: 0;
    display: none;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 0 30px ;
    
    &.active-content {
        display: flex;
        opacity: 1;
    }

    &.reverse {
        flex-direction: row-reverse;
    }

    .left{
        width: 50%; 
        height: 100%;
        max-height: 600px;
        background: #fff;
        box-sizing: border-box;
        border-radius: 5px;
        box-shadow: 5px 5px 5px rgba(0,0,0, 0.2);   
        opacity: 0;
        position: relative;
        animation: fadeIn 3s 0.5s;
        animation-fill-mode: forwards;
        display: flex;
        justify-content: center;

        img {
           width: 100%;
           height: 100%;
           object-fit: cover;
            box-sizing: border-box;
            border-radius: 5px;
            opacity: 0;
            animation: backInLeft 2.3s 0.6s;
            animation-fill-mode: forwards;
        }
    }
    
    &.reverse .left img {
        animation: backInRight 2.3s 0.6s;
        animation-fill-mode: forwards;
    }
    .right {
        width: 50%;
        height: 100%;
        padding: 20px;
        display: flex;
        flex-direction: column;
        justify-content: center;

        &>*{
            opacity: 0;
            animation: fadeInUp 1s 1.6s;
            animation-fill-mode: forwards;
        }
        h2 {
            font-size: 1.7rem;
            padding: 5px;
        }
        p {
            font-size: 1.1rem;
        }
        .button {
            margin: 20px;
            float: right;
        }
    }
}

@media (max-width:800px){
    .content{
        flex-direction: column-reverse;
        justify-content: flex-start;
        padding: 30px ;
        max-height: 60vh;
        
        &.reverse{
            flex-direction: column-reverse;
        }
        .left {
            width: 100%;
            height: 50%;
        }
        .right {
            width: 100%;
            height: 50%;

            h2 {
                font-size: 1.7rem;
            }
            p {
                font-size: 1rem;
            }

            .button{
                margin: 10px;
            }
        }

    }
}

