.galerie-box {
    position: relative;
    top: 0;
    left: 0;
    margin: 0 auto;
    width: 90%;
    max-width: 1300px;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    .galerie-bg {
        width: 100%;
        height: 70%;
        padding: 20px;
        background: #fff;
        border-radius: 5px;
        box-shadow: 5px 5px 5px rgba(0,0,0, 0.2);   
        opacity: 0;
        animation: fadeIn 3s 0.5s;
        animation-fill-mode: forwards;
        display: flex;
        flex-direction: column;
        gap: 1rem;
        justify-content: center;
        align-items: center;
        .mySwiper2 {
            width: 100%;
            height: 80%;
            border-radius: 5px;
            .swiper-wrapper{
                .swiper-slide {
                    width: 100%;
                    height: 100%;
                    opacity: 0;
                    animation: backInRight 2.3s 1.4s;
                    animation-fill-mode: forwards;
                
                img {
                  display: block;
                  width: 100%;
                  height: 100%;
                  object-fit: cover;
                }
              }
            }
        
            .swiper-pagination{
        
              .swiper-pagination-bullet-active{
                  background: rgb(214, 211, 175);
                  opacity: 1;
              }
            }
          }
          .mySwiper {
            
            width: 100%;
            height: 20%;
            .swiper-wrapper{
                .swiper-slide {
                    width: 100%;
                    height: 100%;
                    opacity: 0;
                    animation: backInRight 2.3s 1.4s;
                    animation-fill-mode: forwards;
                    border-radius: 5px;
                
                img {
                    display: block;
                    width: 100%;
                    border-radius: 5px;
                    height: 100%;
                    object-fit: cover;
                }
              }
            }
        
            .swiper-pagination{
        
              .swiper-pagination-bullet-active{
                  background: rgb(214, 211, 175);
                  opacity: 1;
              }
            }
          }
    }
    
}
